<template>
  <div>
    <slot v-if="nodes" name="listMenu">
      <div class="list-group">
        <button
          type="button"
          class="list-group-item list-group-item-action"
          @click="toggleChildren"
          :style="indent"
        >
          {{ label }}
          <span>&nbsp;</span>
          <font-awesome-icon :icon="['fas', iconClasses]" />
        </button>
        <ListMenu
          v-if="showChildren"
          v-for="(node, index) in nodes"
          :nodes="node.nodes"
          :key="index"
          :label="node.label"
          :url="node.url"
          :depth="depth + 1"
          @hide="hide"
        ></ListMenu>
      </div>
    </slot>
    <slot v-else name="listMenu">
      <router-link
        :to="url"
        @click="hide"
        class="list-group-item list-group-item-action"
        :style="indent"
        >{{ label }}</router-link
      >

      <!-- <b-button variant="primary" block @click="hide">Close Sidebar</b-button>-->

      <!--
      <span @click="hide" class="list-group-item list-group-item-action" :style="indent">{{ label }}
      </span>
      -->
    </slot>
  </div>
</template>

<script>
// https://github.com/FortAwesome/vue-fontawesome/issues/215
import { library } from "@fortawesome/fontawesome-svg-core";
import { faCaretUp, faCaretDown } from "@fortawesome/free-solid-svg-icons";

library.add(faCaretUp, faCaretDown);

export default {
  name: "ListMenu",
  props: ["nodes", "label", "url", "depth"],
  data() {
    return {
      showChildren: false,
    };
  },
  computed: {
    iconClasses() {
      if (this.showChildren) {
        return "caret-up";
      } else {
        return "caret-down";
      }
      //return {
      //  'fa-caret-down': !this.showChildren,
      //  'fa-caret-up': this.showChildren
      //}
    },
    indent() {
      return {
        //transform: `translate(${this.depth * 20}px)`
        // padding-left 要加引號
        // "padding-left": `${20 + this.depth * 20}px`
        "padding-left": `${20 + this.depth * 10}px`,
      };
    },
  },
  methods: {
    toggleChildren() {
      this.showChildren = !this.showChildren;
    },
    hide() {
      this.$emit("hide");
    },
  },
  mounted() {
    // console.log("ListMenu");
    // console.log(this.label);
  },
};
</script>

