<template>
  <!-- add v-if to fix "TypeError: Cannot read property 'isbn' of null" -->
  <!-- refer to https://router.vuejs.org/zh/guide/advanced/data-fetching.html#%E5%AF%BC%E8%88%AA%E5%AE%8C%E6%88%90%E5%90%8E%E8%8E%B7%E5%8F%96%E6%95%B0%E6%8D%AE -->
  <b-row v-if="getBook">
    <b-col lg="3" class="mt-3">
      <div class="list-group">
        <li class="list-group-item list-group-item-primary">書目分類</li>
        <TreeMenu :listData="list"></TreeMenu>
      </div>
    </b-col>

    <b-col lg="9">
      <b-row class="mt-3">
        <b-col lg="3" md="12" class="mt-3 justify-content-center">
          <router-link :to="'/product/' + getBook.isbn">
            <img :src="getBook.img" alt class="img-fluid" />
          </router-link>
        </b-col>

        <b-col lg="6" md="12" class="mt-3">
          <ul class="list-unstyled">
            <div itemscope itemtype="http://schema.org/Book">
              <li class="bg-light text-dark">
                <h5>
                  <span class itemprop="name" style>{{ getBook.title }}</span>
                </h5>
              </li>
              <li>
                <span class="info-title">作者&nbsp;&colon;&nbsp;</span>
                <span class="info-content" itemprop="author">{{
                  getBook.author
                }}</span>
              </li>
              <li v-if="haveTranslator">
                <span class="info-title">譯者&nbsp;&colon;&nbsp;</span>
                <span class="info-content" itemprop="author">{{
                  getBook.translator
                }}</span>
              </li>
              <li>
                <span class="info-title">出版社&nbsp;&colon;&nbsp;</span>
                <span class="info-content" itemprop="publisher">{{
                  getBook.pulbisherName
                }}</span>
              </li>
              <li>
                <span class="info-title">出版日期&nbsp;&colon;&nbsp;</span>
                <span class="info-content" itemprop="datePublished">{{
                  getBook.publicationDate
                }}</span>
              </li>
              <li>
                <span class="info-title">頁數&nbsp;&colon;&nbsp;</span>
                <span class="info-content" itemprop="numberOfPages">{{
                  getBook.pages
                }}</span>
              </li>
              <li>
                <span class="info-title">ISBN&nbsp;&colon;&nbsp;</span>
                <span class="info-content" itemprop="isbn">{{
                  getBook.isbn
                }}</span>
              </li>
            </div>
            <li>
              <span class="info-title">定價&nbsp;&colon;&nbsp;</span>
              <span class="info-content">{{ getBook.listPrice }}元</span>
            </li>
            <div itemscope itemtype="http://schema.org/Offer">
              <li>
                <span class="info-title">優惠價&nbsp;&colon;&nbsp;</span>
                <span class="info-content">
                  <span class="text-danger">{{ getBook.discount }}</span
                  >折&nbsp;
                  <span
                    class="text-danger"
                    itemprop="price"
                    v-text="salePrice(getBook)"
                  ></span
                  >元
                </span>
              </li>
            </div>
          </ul>
        </b-col>

        <b-col lg="3" md="12" class="mt-3">
          <div class="text-left">
            <span
              v-text="showInventory(getBook.inventory)"
              :class="showDanger(getBook.inventory)"
            ></span>
          </div>
          <div class="mt-1">
            <b-button
              block
              :variant="btnColor"
              class="text-left"
              :disabled="!getBook.inventory || findInCart"
              @click="addProductToCart(getBook)"
            >
              <font-awesome-icon :icon="['fas', 'shopping-cart']" />&nbsp;{{
                btnCartText
              }}
            </b-button>
          </div>
          <div class="mt-1">
            <b-button
              block
              variant="success"
              class="text-left"
              :disabled="!getBook.inventory"
              @click="checkout(getBook)"
            >
              <font-awesome-icon :icon="['fas', 'money-check-alt']" />&nbsp;結帳
            </b-button>
          </div>
          <div class="mt-1">
            <b-button block variant="warning" class="text-left">
              <font-awesome-icon :icon="['far', 'heart']" />&nbsp;加入追蹤
            </b-button>
          </div>
        </b-col>
      </b-row>

      <!-- new row -->
      <b-col cols="12" class="mt-3">
        <b-alert show variant="primary" class="mt-5">商品描述</b-alert>
        <div v-html="getBook.description"></div>
        <b-alert show variant="primary" class="mt-5" v-if="haveAuthorInfo"
          >作者簡介</b-alert
        >
        <div v-if="haveAuthorInfo" v-html="getBook.authorInfo"></div>
        <b-alert show variant="primary" class="mt-5" v-if="haveOutline"
          >目錄大綱</b-alert
        >
        <div v-html="getBook.outline" v-if="haveOutline"></div>
      </b-col>

      <div data-aos="zoom-in-up">
        <b-col md="12" class="mt-5">
          <SliderThird :images="banner" :dimension="bannerDimension" />
        </b-col>
      </div>
    </b-col>
  </b-row>
</template>
 
<script>
import { mapActions } from "vuex";
import shop from "@/api/shop.js";
import config from "@/common/config";
import TreeMenu from "@/components/TreeMenu.vue";
import { library } from "@fortawesome/fontawesome-svg-core";
import SliderThird from "@/components/SliderThird.vue";
// import { faShoppingCart, faMoneyCheckAlt, faHeart } from '@fortawesome/free-solid-svg-icons'
import {
  faShoppingCart,
  faMoneyCheckAlt,
} from "@fortawesome/free-solid-svg-icons";
import { faHeart } from "@fortawesome/free-regular-svg-icons";
// import store from "@/store";
import { mapGetters } from "vuex";
import { ADD_TO_CART, ALREADY_IN_CART } from "@/common/constant";

library.add(faShoppingCart, faMoneyCheckAlt, faHeart);

export default {
  name: "Product",
  data() {
    return {
      list: config.treeMenu,
      banner: config.banner,
      bannerDimension: "height:180px;",
      // book: null,
      // putIntoCart: false,
      // btnCartText: "放入購物車",
      timeoutID: null,
    };
  },
  props: {},
  components: {
    TreeMenu,
    SliderThird,
  },
  mounted() {},
  methods: {
    fetchData() {
      console.log("fetchData() isbn: " + this.$route.params.isbn);
      // this.book = shop.getProduct(this.$route.params.isbn);
      // console.log(this.book);
      // if (!this.book) {
      //   alert("無此資料");
      //   this.$router.push("/404");
      // }
      this.$store
        .dispatch("products/getProduct", this.$route.params.isbn)
        .then((response) => {
          console.log("response", response);
          // response is from "products.js : resolve(JSON.parse(JSON.stringify(response.data)));" (deep copy)
          // this.book = response;
          // this.book = this.$store.getters["products/getBook"];
        })
        .catch((err) => {
          console.log("err", err);
          // alert(err);
          this.$store.dispatch("modal/setModalMsg", err);
          this.$store.dispatch("modal/setModalShow", true);
          this.$router.push({ name: "index" }).catch((err) => err);
        });

      // this.$store.dispatch("products/addProduct", this.book);
    },
    salePrice(obj) {
      return this.calSalePrice(obj.listPrice, obj.discount);
    },
    calSalePrice(price, discount) {
      return Math.round((price * discount) / 100);
    },
    showInventory(inventory) {
      const INVENTORY = "庫存";
      const OUT_OF_STOCK = "已無庫存";
      if (inventory == 0) {
        return OUT_OF_STOCK;
      } else if (inventory <= 10) {
        return INVENTORY + " = " + inventory;
      } else {
        return INVENTORY + " > 10";
      }
    },
    checkout(product) {
      this.addProductToCart(product);
      this.timeoutID = setTimeout(() => {
        console.log("setTimeout goto shoppingcart");
        //this.$router.push("/shoppingcart");
        this.$router.push("/shoppingcart").catch((err) => err);
      }, 1000);
      console.log("timeoutID", this.timeoutID);
    },
    showDanger(inventory) {
      return {
        "text-danger": inventory == 0 ? true : false,
      };
    },
    // ...mapActions('cart', [
    //   'addProductToCart'
    // ]),
    async addProductToCart(product) {
      let inventoryArray = await this.getServerProductInventory([product.isbn]);
      console.log("inventoryArray", inventoryArray);
      if (Array.isArray(inventoryArray) && inventoryArray.length) {
        // this.book.inventory = inventoryArray[0].inventory;
        this.$store.dispatch("products/changeInventory", inventoryArray[0]);
        if (inventoryArray[0].inventory) {
          this.$store
            .dispatch("cart/addProductToCart", product)
            .then((response) => {
              console.log(
                "addProductToCart - cart/addProductToCart : response",
                response
              );
              // this.btnCartText = response;
              // this.putIntoCart = true;
            })
            .catch((err) =>
              console.log("addProductToCart - cart/addProductToCart : err", err)
            );
        }
      }
    },
    getServerProductInventory(isbnArray) {
      return new Promise((resolve, reject) => {
        this.$store
          .dispatch("products/getServerProductInventory", isbnArray)
          .then((responseData) => {
            console.log("getServerProductInventory responseData", responseData);
            // responseData is JSONArray
            // return responseData;
            resolve(responseData);
          })
          .catch((err) => {
            console.log("getServerProductInventory err", err);
            // alert(err);
            this.$store.dispatch("modal/setModalMsg", err);
            this.$store.dispatch("modal/setModalShow", true);
          });
      });
    },
  },
  watch: {
    // https://router.vuejs.org/zh/guide/advanced/data-fetching.html
    // 如果路由有变化，会再次执行该方法
    $route: "fetchData",
  },
  /*
  beforeRouteEnter(to, from, next) {
    // this.fetchData();
      console.log("beforeRouteEnter isbn: " + to.params.isbn);
      // this.book = shop.getProduct(this.$route.params.isbn);
      // console.log(this.book);
      // if (!this.book) {
      //   alert("無此資料");
      //   this.$router.push("/404");
      // }
      store.dispatch("products/getProduct", to.params.isbn)
        .then((response) => {
          console.log("response", response);
        })
        .catch((err) => {
          console.log("err", err);
        });
  },
  */
  created() {
    console.log("created() isbn: " + this.$route.params.isbn);
    this.fetchData();
    // const cartItem = this.$store.getters["cart/cartProducts"].find(
    //   (item) => item.isbn === this.$route.params.isbn
    // );
    // if (cartItem) {
    //   this.putIntoCart = true;
    //   // this.btnCartText = "已放入購物車";
    // }
  },
  mounted: function () {
    // if (this.$store.getters["products/getBook"]){
    // this.book = this.$store.getters["products/getBook"];
    // }
  },
  beforeDestroy() {
    // console.log("beforeDestroy clearTimeout", +this.timeoutID);
    // clearTimeout(this.timeoutID);
  },
  computed: {
    findInCart() {
      let isbn = this.$store.getters["products/getBook"].isbn;
      let cartProducts = this.$store.getters["cart/cartProducts"];
      if (cartProducts.length) {
        let ret = cartProducts.find((item) => item.isbn === isbn);
        console.log("findInCart", JSON.stringify(ret));
        if (ret) {
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },
    btnCartText() {
      return this.findInCart ? ALREADY_IN_CART : ADD_TO_CART;
    },
    haveTranslator() {
      return (
        this.$store.getters["products/getBook"] &&
        this.$store.getters["products/getBook"].translator.length != 0
      );
    },
    haveAuthorInfo() {
      return (
        this.$store.getters["products/getBook"] &&
        this.$store.getters["products/getBook"].authorInfo.length != 0
      );
    },
    haveOutline() {
      return (
        this.$store.getters["products/getBook"] &&
        this.$store.getters["products/getBook"].outline.length != 0
      );
    },
    btnColor() {
      // return this.putIntoCart ? "dark" : "primary";
      return this.findInCart ? "dark" : "primary";
    },
    ...mapGetters("products", ["getBook"]),
  },
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.info-title {
  /*
  width: 50px;
  display: inline-block !important;
  */
  display: block;
  width: 100px;
  color: #3d3f46;
  position: absolute;
}
.info-content {
  padding-left: 100px;
  display: block;
}
</style>